<template>
	<view class="body">
		<image class="banner" src="../../static/review/banner.png" mode="widthFix"></image>
		<view class="content">
			<!-- 介绍部分 -->
			<view class="partOne">
				<view class="tabList">
					<view :class="{cell:true, checked:resetCurrent==index}" v-for="(item,index) in data.retest" :key="index"
						@click="changeTab(index)">{{item.title}}</view>
				</view>
				<image class="produce" src="../../static/review/pro_banner.jpg" mode="widthFix"></image>
				<image class="contentImg" :src="data.retest[resetCurrent].content" mode="widthFix"></image>
			</view>
			<!-- 套餐部分 -->
			<view class="partTwo">
				<view class="title">选择复试套餐</view>
				<view class="list">
					<view :class="{cell:true, checked:goodsCurrent==index}" v-for="(item,index) in data.goods" :key="index"
						@click="changeThail(index)">
						<view class="type">{{item.title}}</view>
						<view class="titles">{{item.sub_title}}</view>
						<view class="price">{{item.price}}</view>
					</view>
				</view>
			</view>
			<!-- 按钮部分 -->
			<view class="partThree">
				<button open-type="contact">咨询客服</button>
				<button class="buy" @click="show=true">立即购买</button>
			</view>
			<u-popup :show="show" mode="bottom" @close="close" @open="open" :closeOnClickOverlay="true">
				<view class="popupArea">
					<view class="packages">
						<view class="title">套餐信息</view>
						<view class="type">
							<view class="typeTitle">{{data.goods[goodsCurrent].title}}</view>
							<view class="text">描述： {{ data.goods[goodsCurrent].sub_title }}</view>
							<view class="price">价格： <view>{{ data.goods[goodsCurrent].price }}</view>
							</view>
						</view>
					</view>
					<view class="buttons">
						<button @click="payfor">立即支付</button>
						<button class="doughButton" v-if="data.goods[goodsCurrent].is_group==1" @click="dough">拼团支付<view>（{{data.goods[goodsCurrent].group_price}}元）</view></button>
					</view>
				</view>
			</u-popup>
		</view>
		<!-- 拼团模块 -->
		<view class="dough" v-if="showDough">
			<view class="moudle">
				<view class="close" @click="showDough=false">×</view>
				<view class="title" v-show="group_id">邀请成功，开始拼团</view>
				<view class="title" v-show="!group_id">支付成功，开始拼团</view>
				<view class="text"><u-count-down :time="groupGood.end_time * 1000" format="HH:mm:ss"></u-count-down>后结束</view>
				<view class="numberList">
					<view class="mine" v-for="(item,index) in groupGood.group" :key="index">
						<image :src="item.avatar || '../../static/mine/user_head.png'"></image>
						<view class="icon" v-if="item.is_group_captain==1">团长</view>
					</view>
					<view class="other" v-for="item in 3-groupGood.group.length" :key="item"></view>
				</view>
				<view class="tip">点击我的拼团查看</view>
				<button class="button" @click="addInvite" v-show="group_id">加入拼团</button>
				<button class="button" open-type="share" v-show="!group_id">邀请好友拼团</button>
			</view>
		</view>
		<!-- 邀请拼团模块 -->
		<view class="dough" v-if="showIntite">
			<view class="moudle">
				<view class="close" @click="showIntite=false">×</view>
				<view class="title">邀请成功，开始拼团</view>
				<view class="text"><u-count-down :time="time * 1000" format="HH:mm:ss"></u-count-down>后结束</view>
				<!-- <view class="numberList">
					<view class="mine" v-for="(item,index) in groupGood.group" :key="index">
						<image :src="item.avatar || '../../static/mine/user_head.png'"></image>
						<view class="icon" v-if="item.is_group_captain==1">团长</view>
					</view>
					<view class="other" v-for="item in 3-groupGood.group.length" :key="item"></view>
				</view> -->
				<view class="goodsInfo">
					<view class="name">商品名称:{{name}}</view>
					<view class="price">价格:{{price}}</view>
				</view>
				<button class="button" @click="addInvite">加入拼团</button>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		review, directPay, groupPay, addGroup
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				show: false, //是否显示商品信息
				showDough: false, //弹出拼团
				showIntite:false, //弹出邀请拼团
				resetCurrent:5,
				goodsCurrent:0,
				token:'',
				group_id:'',
				name:'',
				price:'',
				time:'',
				data:{},
				groupGood:{}
			}
		},
		onShareAppMessage() {
			//分享
			wx.showShareMenu({
		      withShareTicket: true,
		      menus: ['shareAppMessage', 'shareTimeline']
		     })
		    return {
			  title: '考研调剂平台小程序',
		      path: '/pages/review/review?group_id='+this.groupGood.group_id+'&name='+this.groupGood.goods.sub_title+'&price='+this.groupGood.goods.group_price+'&time='+this.groupGood.end_time,
			  imageUrl:'/static/review/help_share.jpg', //设置分享图片
		      // promise 
		    }
		},
		onShareTimeline: function () {
			//分享监听事件
			return {
			  title: '	考研调剂平台小程序',
			}
		},
		onLoad(option) {
			if(option.group_id){
				this.goods_id = option.group_id
				this.name = option.name
				this.price = option.price
				this.time = option.time
			}
		},
		onShow() {
			this.token = store.state.vuex_token
			if(this.token && this.goods_id){
				this.showIntite = true
				console.log('this.showIntite:',this.showIntite)
			}
			if(this.token==''){
				this.$u.toast('请先登录')
			}
			review().then(res=>{
				console.log(res)
				if(res.data.code==-1){
					this.$u.toast(res.data.data)
				}
				this.data = res.data.data
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 选择tab
			changeTab(index) {
				this.resetCurrent = index
			},
			// 选择套餐
			changeThail(index) {
				this.goodsCurrent = index
			},
			// 打开弹出层
			open() {
			  // this.show = false
			  console.log('open');
			},
			// 关闭弹出层
			close() {
			  this.show = false
			  console.log('close');
			},
			// 购买
			payfor() {
				let that = this
				directPay({token:this.token, goods_id:this.data.goods[this.goodsCurrent].id}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: String(res.data.data.data.timeStamp),
						nonceStr: res.data.data.data.nonceStr,
						package: 'prepay_id='+res.data.data.data.prepay_id,
						signType: 'MD5',
						paySign: res.data.data.data.sign,
					    success: function (res) {
					        let rawdata = JSON.parse(res.rawdata);
							console.log("支付成功");
							that.$u.toast("购买成功")
					    },
					    fail: function (err) {
					        console.log('支付失败:' + JSON.stringify(err));
					    }
					});
				}).catch(err=>{
					console.log(err)
				})
			},
			// 拼团
			dough() {
				let that = this
				groupPay({token:this.token, goods_id:this.data.goods[this.goodsCurrent].id}).then(res=>{
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}
					if(res.data.data.type=="group"){
						this.groupGood = res.data.data
						this.showDough = true //显示拼团弹窗
						this.show = false //关闭商品信息
					}else{
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: String(res.data.data.data.timeStamp),
							nonceStr: res.data.data.data.nonceStr,
							package: 'prepay_id='+res.data.data.data.prepay_id,
							signType: 'MD5',
							paySign: res.data.data.data.sign,
							success: function (res) {
								console.log("支付成功");
								that.$u.toast("购买成功")
							},
							fail: function (err) {
								console.log('支付失败:' + JSON.stringify(err));
							}
						});
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 邀请拼团
			addInvite(){
				addGroup({token:this.token, group_id:this.goods_id}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: String(res.data.data.data.timeStamp),
						nonceStr: res.data.data.data.nonceStr,
						package: 'prepay_id='+res.data.data.data.prepay_id,
						signType: 'MD5',
						paySign: res.data.data.data.sign,
						success: function (res) {
							console.log("支付成功");
							that.$u.toast("购买成功")
						},
						fail: function (err) {
							console.log('支付失败:' + JSON.stringify(err));
						}
					});
				}).catch(err=>{
					console.log(err)
				})
			},
		}
	}
</script>

<style lang="scss">
	.body {
		width: 100vw;
		min-height: 100vh;
		background-color: #f7f6fb;
		position: relative;
		overflow: auto;
		padding-bottom: 30rpx;

		.banner {
			width: 100vw;
		}

		.content {
			width: 100vw;
			top: 200rpx;
			position: absolute;
			.partOne {
				width: 90%;
				border-radius: 20rpx;
				padding: 30rpx 0;
				background-color: #fff;
				margin: 0 5%;

				.tabList {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					width: 90%;
					margin: 0 5%;

					.cell {
						width: 26%;
						color: #fff;
						font-size: 26rpx;
						text-align: center;
						background-color: #ccc;
						padding: 12rpx 8rpx;
						border-radius: 50rpx;
						margin-bottom: 30rpx;
						white-space: nowrap;
						overflow: scroll;
						margin-right: 30rpx;
					}
					.cell:nth-child(3n){
						margin-right: 0;
					}
					.checked {
						background-color: #cb3025;
					}
				}

				.produce {
					width: 90%;
					margin: 30rpx 5%;
					border-radius: 20rpx;
				}

				.contentImg {
					width: 90%;
					margin: 0 5%;
				}
			}

			.partTwo {
				width: 90%;
				padding: 30rpx 0;
				border-radius: 20rpx;
				background-color: #fff;
				margin: 30rpx 5%;

				.title {
					font-size: 32rpx;
					font-weight: 500;
					margin: 0 30rpx;
				}

				.list {
					display: flex;
					flex-wrap: wrap;
					margin: 30rpx;

					.cell {
						width: 30%;
						background-color: #f7f6fb;
						border-radius: 20rpx;
						padding: 30rpx 0;
						text-align: center;
						font-size: 28rpx;
						color: #785a3c;
						margin-bottom: 30rpx;
						margin-right: 30rpx;
						.type {
							width: 120rpx;
							background-color: #785a3c;
							color: #fff;
							border-radius: 50rpx;
							line-height: 50rpx;
							margin: 0 auto;
						}

						.titles {
							line-height: 68rpx;
							width: 90%;
							overflow: hidden;
							text-overflow: ellipsis;
							margin: 0 5%;
							white-space: nowrap;
						}

						.price {
							color: #e20000;
							font-size: 54rpx;
							font-weight: 700;
							width: 100%;
							text-overflow: ellipsis;
							white-space: nowrap;
							overflow: hidden;
						}

						.price::before {
							content: '￥';
							color: #e20000;
							font-size: 24rpx;
						}
					}

					.checked {
						background-color: #fce9d2;
					}
				}
			}

			.partThree {
				width: 90%;
				margin: 30rpx 5%;
				display: flex;
				justify-content: space-between;

				button {
					width: 46%;
					color: #fff;
					text-align: center;
					font-size: 34rpx;
					line-height: 88rpx;
					background-color: #ffd656;
					border-radius: 50rpx;
				}

				.buy {
					background-color: #ff8d8a;
				}
			}
			
			.popupArea{
				background-color: #fff;
				padding: 30rpx;
				.packages{
					margin: 0;
					padding: 0;
					.title{
						display: flex;
						align-items: center;
						font-size: 28rpx;
						font-weight: 600;
						margin-bottom: 20rpx;
					}
					.title::before{
						content: '';
						display: inline-block;
						width: 8rpx;
						height: 28rpx;
						border-radius: 50rpx;
						background-color: #ffd656;
						margin-right: 16rpx;
					}
					.type{
						background-color: #fce9d2;
						border-radius: 20rpx;
						padding: 30rpx;
						font-size: 28rpx;
						position: relative;
						overflow: hidden;
						.typeTitle{
							margin-bottom: 30rpx;
							color: #fff;
							width: 150rpx;
							font-size: 24rpx;
							line-height: 48rpx;
							text-align: center;
							border-radius: 50rpx;
							background-color: #785a3c;
						}
						.text{
							color: #785a3c;
							margin-bottom: 20rpx;
						}
						.price{
							color: #785a3c;
							view{
								color: #e20000;
								font-size: 38rpx;
								font-weight: 600;
								display: inline-block;
							}
							view::before{
								content: "￥";
								font-size: 20rpx;
							}
						}
					}
				}
				.buttons{
					display: flex;
					margin-top: 60rpx;
					button{
						flex: 5;
						line-height: 88rpx;
						font-size: 32rpx;
						color: #fff;
						border-radius: 50rpx;
						background-color: #ffd656;
					}
					.doughButton{
						flex: 7;
						background-color: #ee0000;
						margin-left: 20rpx;
						view{
							display: inline-block;
							font-size: 28rpx;
						}
					}
				}
			}
		}
		.dough{
			width: 100vw;
			height: 100vh;
			position: fixed;
			top: 0;
			left: 0;
			background: rgba(0, 0, 0, .3);
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 999;
			.moudle{
				width: 80%;
				position: relative;
				padding: 20rpx 0;
				text-align: center;
				font-size: 28rpx;
				background-color: #fff;
				border-radius: 20rpx;
				.goodsInfo{
					font-size: 28rpx;
					.name{
						margin-left: 20rpx
					}
					.price{
						color: #e20000;
						margin-left: 20rpx
					}
				}
				.title{
					font-size: 34rpx;
					font-weight: 600;
					line-height: 68rpx;
				}
				.text{
					font-size: 24rpx;
					color: #a6a6a6;
					display: flex;
					align-items: center;
					justify-content: center;
					.u-count-down__text{
						color: #a6a6a6;
						font-size: 24rpx !important;
						margin-right: 2rpx;
					}
				}
				.numberList{
					height: 150rpx;
					margin: 20rpx 120rpx;
					display: flex;
					justify-content: space-around;
					align-items: center;
					.mine{
						position: relative;
						image{
							width: 90rpx;
							height: 90rpx;
							border-radius: 50%;
							border: 1rpx solid #fed555;
						}
						.icon{
							position: absolute;
							left: -10rpx;
							top: 0rpx;
							font-size: 20rpx;
							padding: 2rpx 8rpx;
							background-color: #ffd656;
							color: #fff;
							border: 1rpx solid #fff;
							border-radius: 50rpx;
						}
					}
					.other{
						border: 1rpx dashed #ccc;
						width: 90rpx;
						height: 90rpx;
						border-radius: 50%;
						background-image: url(../../static/mine/question_icon.png);
						background-repeat: no-repeat;
						background-position: center;
						background-size:  30rpx;
					}
				}
				.tip{
					font-size: 26rpx;
					color: #6d6d6d;
					line-height: 58rpx;
				}
				.button{
					background-color: #ffd656;
					color: #fff;
					font-size: 36rpx;
					line-height: 78rpx;
					border-radius: 50rpx;
					width: 90%;
					margin: 20rpx auto;
					text-align: center;
				}
				.close{
					position: absolute;
					right: -20rpx;
					top: -20rpx;
					width: 50rpx;
					line-height: 50rpx;
					text-align: center;
					font-size: 30rpx;
					border-radius: 50%;
					color: #939393;
					background-color: #e1e1e1;
				}
			}
			
		}
	}
</style>
